html,
body,
:root {
  background: hsl(0, 0%, 0%);
  width: 100vw;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scrollbar-color: hsla(0, 0%, 0%, 0%);
  scrollbar-width: none;
}

.App {
  position: relative;
  font-family: sans-serif;
  text-align: center;
  overflow: hidden;
  display: flex;
  flex-flow: column nowrap;
  /*display: grid;
  grid-template-rows: [card] auto [controls] 1fr;
  grid-template-columns: [margin-left] 1fr [card] clamp(300px, 100vh, 90vw) [margin-right] 1fr;
  grid-row-gap: 10px;*/
  gap: 0.5rem;
  justify-content: flex-start;
  align-items: center;
  width: 100vw;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
}

.controls {
  position: absolute;
  bottom: 68px;
  background: hsl(0, 0%, 20%);
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
}

.related-tags {
  display: flex;
  flex-flow: row wrap;
  gap: 0.5rem;
}

.related-tags button {
  border: 2px dashed hsl(0, 0%, 80%);
  background: hsla(0, 0%, 0%, 0%);
  color: hsl(0, 0%, 80%);
}

h1 {
  font-size: clamp(32px, 20vmin, 48px);
  margin: 0;
}

h2 {
  font-size: clamp(24px, 15vmin, 32px);
  margin: 0;
  color: hsl(0, 0%, 50%);
}

div.list-view {
  position: absolute;
  display: flex;
  flex-flow: row nowrap;
  align-items: stretch;
  justify-content: stretch;
  /*top: 0;
  left: 0;
  bottom: 0;
  right: 0;*/
  overflow-x: hidden;
  overflow-y: hidden;
  width: 100vw;
  height: 100vh;
  /* background: hsla(0, 0%, 0%, 50%);
  /* grid-column: margin-left / span 3;
  grid-row: card / span 2;*/
  z-index: 4;
  background: hsla(0, 0%, 0%, 50%);
  transition: background 0.5s, left 0s, right 0s;
}

div.left.list-view {
  /*display: grid;
  grid-template-columns: auto 1fr;*/
  top: 0;
  left: 0;
  bottom: 0;
  transform-origin: left;
}

div.left.list-view.hidden {
  /*left: -100%;
  right: auto;*/
  width: 0vw;
  background: hsla(0, 0%, 0%, 0%);
  transition: background 0.5s, width 0s 0.5s; /*left 0s 0.5s, right 0s 0.5s;*/
}

div.right.list-view {
  top: 0;
  bottom: 0;
  right: 0;
  transform-origin: right;
}

div.right.list-view.hidden {
  width: 0vw;
  background: hsla(0, 0%, 0%, 0%);
  transition: background 0.5s, width 0s 0.5s;
}

.exit-overlay {
  flex: 1 1 0;
  /*grid-column: auto / -1;
  grid-row: 1 / -1;*/
  width: 100px;
  height: 100%;
}

div.panel {
  width: clamp(200px, 70vw, 300px);

  height: 100%;
  margin: 0;
  padding: 2vmin;
  background: hsl(0, 0%, 10%);
  box-shadow: 10px 0px 50px 5px hsla(0, 0%, 0%, 50%);
  text-align: left;
  overflow-x: hidden;
  overflow-y: scroll;
  scrollbar-color: hsla(0, 0%, 0%, 0%);
  scrollbar-width: none;
  transition-duration: 0.5s;
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-start;
  justify-content: flex-start;
  /*grid-column: auto / span 1;*/
}

div.panel h2 {
  padding-left: 1.3rem;
  padding-right: 1.3rem;
  text-transform: uppercase;
}

div.right div.panel h2 {
  padding-left: 0;
  padding-right: 0;
  text-transform: uppercase;
}

div.right div.panel {
  gap: 1rem;
}

