div.left.list-view.hidden div.panel {
  transform: translateX(-100%);
}

div.right.list-view.hidden div.panel {
  transform: translateX(100%);
}

ol.panel li {
  grid-column: 1 / span 1;
  padding: 0.1rem 2rem 0.1rem 0.2rem;
  color: hsl(0, 0%, 100%);
  cursor: pointer;
}

ol.panel li:hover {
  background-color: hsl(280, 50%, 50%);
}

