  background-color: hsl(280, 50%, 50%);
  cursor: auto;
}*/

.card-container {
  position: relative;
  /*grid-column: card / span 1;
  grid-row: card / span 1;*/
  width: clamp(300px, 100vmin, 600px);
  height: clamp(300px, 100vmin, 600px);
  aspect-ratio: 1 / 1;
  justify-self: center;
  overflow: visible;
}

.card {
  position: absolute;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  background: hsl(0, 0%, 80%);
  border-color: hsl(0, 0%, 100%);
  border-style: solid;
  border-width: 2vmin;
  border-radius: 5vmin;
  aspect-ratio: 1 / 1;
  top: 10px;
  left: 10px;
  bottom: 10px;
  right: 10px;
  z-index: 0;
  overflow: hidden;
  padding: 0px;
  box-shadow: 0px 0px 10px 0px hsl(0, 0%, 0%);
  cursor: pointer;
  /*transform-origin: bottom center;*/
}

.top.card {
  transition-property: transform;
  transition-duration: 0.6s;
}

.top.card.front {
  z-index: 3;
  transform: rotateY(0deg);
}

.top.card.back {
  z-index: 2;
  transform: rotateY(180deg);
}

.card-container:hover .top.card.front {
  transform: rotateY(20deg);
}

.card-container:hover .top.card.back {
  transform: rotateY(160deg);
}

.pause,
.top.card.pause,
.bottom.card.front.pause,
.bottom.card.back.pause {
  animation-name: none;
}

.bottom.card.front,
.bottom.card.front.pause,
.bottom.card.back.pause {
  opacity: 0;
}

.play {
  animation-play-state: running;
  animation-duration: 1s;
  animation-iteration-count: 1;
  opacity: 1;
}

.top.card.play {
  animation-name: swap-top;
}

.bottom.card.front.play {
  animation-name: swap-bottom-front;
}

.bottom.card.back.play {
  animation-name: swap-bottom-back;
}

.img {
  background-color: white;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 2vmin;
}

.controls {
  grid-column: card / span 1;
  grid-row: controls / span 1;
  display: flex;
  flex-flow: row nowrap;
  align-items: start;
  justify-content: center;
  gap: 0.5rem;
}

.button {
  display: flex;
  gap: 0.5rem;
  font-size: 1rem;
  border: none;
  background: hsl(0, 0%, 80%);
  padding: 0.5rem 0.8rem 0.5rem 0.8rem;
  border-radius: 5px;
  cursor: pointer;
  transition-duration: 0.2s;
}

.button:hover,
.button.active {
  background: hsl(280, 50%, 50%);
  color: hsl(0, 0%, 100%);
}

.button:active {
  transform: scale(0.9);
}

.button:hover svg path {
  fill: hsl(0, 0%, 100%);
}

label.button input {
  display: none;
}

@keyframes swap-top {
  0% {
    transform: translate3d(0%, 0%, 0px);
  }
  30% {
    transform: translate3d(55%, 0%, 0px);
  }
  31% {
    transform: translate3d(55%, 0%, 0px);
  }
  100% {
    transform: translate3d(0%, 0%, 0px);
  }
}

@keyframes swap-bottom-front {
  0% {
    transform: translate3d(0%, 0%, 50px);
    z-index: 4;
  }
  30% {
    transform: translate3d(-55%, 0%, 0px);
    z-index: 4;
  }
  31% {
    transform: translate3d(-55%, 0%, 0px);
    z-index: 1;
  }
  100% {
    transform: translate3d(0%, 0%, 0px);
    z-index: 1;
  }
}

@keyframes swap-bottom-back {
  0% {
    transform: translate3d(0%, 0%, 30px);
    z-index: 3;
  }
  30% {
    transform: translate3d(-55%, 0%, 0px);
    z-index: 3;
  }
  31% {
    transform: translate3d(-55%, 0%, 0px);
    z-index: 0;
  }
  100% {
    transform: translate3d(0%, 0%, 0px);
    z-index: 0;
  }
}
